import divider from '@/components/divider.vue';
export default {
	components: {
		divider
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			phone: "17677777777",
			newPhone: "",
			yzm: "获取验证码",
			second: 60
		}
	},
	methods: {
		//获取验证码，记得做防止重复点击
		getCode() {
			var interval = setInterval(() => {
				let times = --this.second
				this.second = times < 10 ? '0' + times : times ,//小于10秒补 0
				this.yzm=this.second+"秒后重新获取"
			}, 1000)
			setTimeout(() => {
				clearInterval(interval)
				this.second = 60
				this.yzm="重新获取验证码"
			}, 60000)
		}
	}
}
